<template>
  <!-- <transition name="fade"> -->
  <div class="edit-container">
    <!-- <router-view /> -->
    <van-nav-bar
      class="page-nav-bar"
      title="个人信息"
      left-arrow
      @click-left="$router.back()"
      v-on:click="show = !show"
    >
    </van-nav-bar>
    <!-- 图片选择框 -->
    <input type="file" hidden ref="file" @change="onPhotoChange" />

    <van-cell title="头像" is-link @click="$refs.file.click()">
      <template #default>
        <van-image fit="cover" round :src="userProfile.photo" />
      </template>
    </van-cell>
    <van-cell
      title="昵称"
      :value="userProfile.name"
      is-link
      to="/my/edit/name"
    />
    <van-cell
      title="性别"
      :value="userProfile.gender === 1 ? '女' : '男'"
      is-link
      @click="showGender = true"
    />
    <van-cell
      title="生日"
      :value="userProfile.birthday"
      is-link
      @click="showDate = true"
    />

    <!-- 性别修改弹出层 -->
    <van-popup
      v-model="showGender"
      position="bottom"
      :style="{ height: '35%' }"
    >
      <edit-gender @close="editGender" :userProfile="userProfile" />
    </van-popup>
    <!-- 修改生日对话框 -->
    <van-popup v-model="showDate" position="bottom" :style="{ height: '35%' }">
      <edit-date :userProfile="userProfile" @closeDate="editBirthday" />
    </van-popup>
    <!-- 修改头像对话框 -->
    <van-popup
      v-model="showUpdatePhoto"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <edit-photo
        v-if="showUpdatePhoto"
        :photo="photoURL"
        @close="showUpdatePhoto = false"
        @update-avatar="userProfile.photo = $event"
      />
    </van-popup>
  </div>
  <!-- </transition> -->
</template>

<script>
import { getProfile } from '@/api/user'
import EditGender from '@/views/Editing/compoments/edit_gender'
import EditDate from '@/views/Editing/compoments/edit-date'
import EditPhoto from '@/views/Editing/compoments/edit-photo'
export default {
  name: 'EditingIndex',
  data () {
    return {
      userProfile: {},
      showGender: false,
      showDate: false,
      showUpdatePhoto: false,
      photoURL: null
    }
  },
  created () {
    this.getUserData()
  },
  components: {
    EditGender,
    EditDate,
    EditPhoto
  },
  methods: {
    async getUserData () {
      const res = await getProfile()
      console.log(res)
      this.userProfile = res.data
    },
    //  关闭修改性别对话框
    editGender (v) {
      if (v) {
        this.userProfile.gender = v.gender
      }
      this.showGender = false
    },
    //  关闭修改生日对话框
    editBirthday (v) {
      if (v) {
        this.userProfile.birthday = v
      }
      this.showDate = false
    },
    onPhotoChange () {
      const file = this.$refs.file.files[0]
      this.photoURL = window.URL.createObjectURL(file)
      this.showUpdatePhoto = true
      this.$refs.file.value = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.edit-container {
  height: 100%;
  ::v-deep .van-nav-bar__left {
    .van-nav-bar__arrow {
      font-size: 34px;
      color: #fff;
    }
  }
  .van-image {
    width: 60px;
    height: 60px;
  }
}
</style>
