<template>
  <div class="gender-item">
    <van-picker
      title="性别"
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      item-height="30px"
      :default-index="userProfile.gender"
    />
  </div>
</template>

<script>
import { editProfile } from '@/api/user'
export default {
  name: 'EditGenderIndex',
  props: {
    userProfile: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      columns: ['男', '女']
    }
  },
  methods: {
    // 提交事件
    async onConfirm (value, index) {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 紧张背景点击
        duration: 0 // 持续展示
      })
      const res = await editProfile({
        gender: index
      })
      // console.log(res)
      if (res.message === 'OK') {
        this.$toast.success('更新成功')
      } else {
        this.$toast.fail('更新失败')
      }
      this.$emit('close', res.data)
    },
    //  取消事件
    onCancel () {
      this.$emit('close')
    }
  }

}
</script>

<style lang="scss" scoped>
</style>
