<template>
  <div class="date-item">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title="选择年月日"
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="onCancel"
      @confirm="onConfirm"
      item-height="30px"
    />
  </div>
</template>

<script>
import { editProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
  name: 'EditDateIndex',
  props: {
    userProfile: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      minDate: new Date(1970, 1, 1),
      maxDate: new Date(),
      currentDate: new Date(this.userProfile.birthday)
    }
  },
  methods: {
    onCancel () {
      this.$emit('closeDate')
    },
    async onConfirm (v) {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 紧张背景点击
        duration: 0 // 持续展示
      })
      const res = await editProfile({
        birthday: dayjs(v).format('YYYY-MM-DD')
      })
      // console.log(res)
      if (res.message === 'OK') {
        this.$toast.success('更新成功')
      } else {
        this.$toast('更新失败')
      }
      this.$emit('closeDate', res.data.birthday)
    }
  }
}
</script>

